import { h, Component } from 'preact';
import Store from 'store/store';
import 'store/bolt/userFeedbackModal';
import DoubleTickets from 'components/bolt/icons/doubleTickets';
import Close from 'components/bolt/icons/close';
import Check from 'components/bolt/icons/check';
import { joinClasses } from 'utils/utils';
import { navigateInternally } from 'utils/bolt';
import { userFeedbackApi } from 'utils/bolt/api';
import s from 'components/bolt/userFeedbackModal/userFeedbackModal.sss';

export default class UserFeedbackModal extends Component {
	constructor(props) {
		super(props);
		this.INITIAL_STEP = 'initialStep';
		this.BOOKED_TICKETS_STEP = 'bookedTicketsStep';
		this.WILL_BOOK_STEP = 'willBookStep';
		this.BOOKING_ISSUE_STEP = 'bookingIssueStep';
		this.THANK_YOU_STEP = 'thankYouStep';
		this.changeStep = this.changeStep.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.onTicketConversion = this.onTicketConversion.bind(this);

		this.initialState = {
			step: this.INITIAL_STEP,
			bookingIssue: '',
			timeToClose: this.getInitialTimeToClose(),
			showSpinner: false,
			feedbackDetails: {},
			nothingSelectedError: false
		};
		this.state = this.initialState;
		Store.on('TICKET_CONVERSION', this.onTicketConversion);
	}

	componentWillUnmount() {
		clearTimeout(this.closeTimer);
		clearTimeout(this.timerReset);
		clearTimeout(this.showModalTimer);
		Store.off('TICKET_CONVERSION', this.onTicketConversion);
	}

	getInitialTimeToClose() {
		const copy = this.props?.appData?.copy?.baseCopy || {};
		// timeToClose is in seconds
		let timeToClose = copy.$FEEDBACK_CLOSE_TIMER?.match(/<(\d)>/);
		if (timeToClose) {
			timeToClose = +timeToClose[1];
		} else {
			timeToClose = 5;
		}
		return timeToClose;
	}

	onTicketConversion(data) {
		const meta = this.props?.appData?.meta || {};
		this.setState({
			feedbackDetails: {
				movieTitle: meta.title?.en || meta.title?.locale,
				movieSlug: meta.title?.slug,
				movieId: data?.screening?.movieId,
				screeningId: data?.screening?.id,
				provider: data?.provider,
				deeplink: data?.screening?.link?.[data?.provider],
				studio: meta.studio?.name || meta.studio?.slug,
				region: meta.region?.slug,
				vanity: meta.url
			}
		});
		this.showModalTimer = setTimeout(() => {
			Store.emit('SHOW_USER_FEEDBACK_MODAL', true);
		}, 1000);
	}

	onPromoImageClick(promoImage) {
		if (!promoImage?.link) return;
		navigateInternally(promoImage.link);
		this.closeModal();
	}

	closeModal() {
		const { showSpinner, step } = this.state;
		if (showSpinner) return;
		if (step !== this.THANK_YOU_STEP) {
			Store.emit('USER_FEEDBACK_MODAL_DISMISSED');
		}
		clearTimeout(this.closeTimer);
		Store.emit('SHOW_USER_FEEDBACK_MODAL', false);
		this.timerReset = setTimeout(() => {
			this.apiError = false;
			this.setState(this.initialState);
		}, 300);
	}

	changeStep(step) {
		this.setState({ step });
	}

	selectIssue(issue) {
		if (this.state.showSpinner) return;
		const copy = this.props?.appData?.copy?.baseCopy || {};
		const bookingIssue = issue ? issue : copy.$FEEDBACK_OTHER;
		this.setState({ bookingIssue, nothingSelectedError: false });
	}

	async sendFeedback({ bookingIssue, ticketQuantity, ticketsBooked }) {
		if (!bookingIssue && !ticketQuantity) {
			this.setState({ nothingSelectedError: true });
			return;
		}
		if (this.state.showSpinner) return;

		this.setState({ showSpinner: true, nothingSelectedError: false });

		const { feedbackDetails } = this.state;
		const body = {
			movie_title: feedbackDetails.movieTitle,
			studio: feedbackDetails.studio,
			region: feedbackDetails.region,
			vanity: feedbackDetails.vanity,
			timestamp: new Date().toISOString(),
			movie_slug: feedbackDetails.movieSlug,
			screening_id: feedbackDetails.screeningId,
			movie_id: feedbackDetails.movieId,
			screening_deeplink: feedbackDetails.deeplink,
			data_provider: feedbackDetails.provider
		};
		if (bookingIssue) {
			body.feedback_type = 'issue';
			body.issue = bookingIssue;
			Store.emit('USER_FEEDBACK_MODAL_ISSUE_SUBMITTED');
		} else if (ticketQuantity) {
			body.feedback_type = 'tickets';
			body.num_tickets = ticketQuantity;
			body.already_booked = ticketsBooked;
			Store.emit('USER_FEEDBACK_MODAL_TICKET_FEEDBACK_SUBMITTED');
		}

		const res = await userFeedbackApi({
			method: 'post',
			path: '/send-to-googlesheet',
			body
		});

		this.setState({ showSpinner: false });

		if (res.result !== 'ok') {
			this.apiError = true;
		}
		this.changeStep(this.THANK_YOU_STEP);
		this.startCloseTimer();
	}

	startCloseTimer() {
		const { timeToClose } = this.state;
		this.closeTimer = setTimeout(() => {
			this.setState({ timeToClose: timeToClose - 1 });
			if (timeToClose - 1 < 1) {
				this.closeModal();
				return;
			}
			this.startCloseTimer();
		}, 1000);
	}

	renderInitialStep() {
		const copy = this.props?.appData?.copy?.baseCopy || {};
		const promoImage = this.props?.appData?.options?.userFeedbackModal?.promoImage;

		const buttons = [
			{
				copy: copy.$FEEDBACK_STILL_BROWSING,
				onClick: this.closeModal
			},
			{
				copy: copy.$FEEDBACK_YES,
				onClick: () => this.changeStep(this.BOOKED_TICKETS_STEP)
			},
			{
				copy: copy.$FEEDBACK_WILL_BOOK_LATER,
				onClick: () => this.changeStep(this.WILL_BOOK_STEP)
			}
		];

		return (
			<div class={s.content}>
				<DoubleTickets class={s.doubleTickets} />
				<span class={s.title}>{copy.$FEEDBACK_DID_YOU_BOOK_TICKETS}</span>
				{buttons.map(btn => (
					<button class={s.longBtn} onClick={btn.onClick} key={`feedback-btn-${btn.copy}`}>
						{btn.copy}
					</button>
				))}
				<button class={s.problem} onClick={() => this.changeStep(this.BOOKING_ISSUE_STEP)}>
					{copy.$FEEDBACK_PROBLEM_ON_PAGE}
				</button>
				{!!promoImage && (
					<button class={s.promoImageBtn} onClick={() => this.onPromoImageClick(promoImage)}>
						<img src={promoImage.src} alt={promoImage.alt} />
					</button>
				)}
			</div>
		);
	}

	renderTicketQuantityStep(step) {
		const copy = this.props?.appData?.copy?.baseCopy || {};
		const title = step === this.BOOKED_TICKETS_STEP ? copy.$FEEDBACK_HOW_MANY_TICKETS_PURCHASED : copy.$FEEDBACK_HOW_MANY_TICKETS_WILL_PURCHASE;
		const { showSpinner } = this.state;
		return (
			<div class={s.content}>
				<DoubleTickets class={s.doubleTickets} />
				<span class={s.title}>{title}</span>
				<div class={s.quantities}>
					{Array(10).fill('').map((a, i) => {
						const quantity = i === 9 ? `${i + 1}+` : i + 1;
						return (
							<button
								class={s.quantityBtn}
								key={`step-quantity-${i}`}
								onClick={() => this.sendFeedback({ ticketQuantity: quantity, ticketsBooked: step === this.BOOKED_TICKETS_STEP })}
							>
								{quantity}
							</button>
						);
					})}
				</div>
				<div class={joinClasses(s.spinner, s.ticketStepSpinner, showSpinner && s.show)} />
			</div>
		);
	}

	renderBookingIssueStep() {
		const copy = this.props?.appData?.copy?.baseCopy || {};
		const { bookingIssue, showSpinner, nothingSelectedError } = this.state;

		const buttons = [
			{
				copy: copy.$FEEDBACK_LINK_BROKEN,
				onClick: () => this.selectIssue(copy.$FEEDBACK_LINK_BROKEN)
			},
			{
				copy: copy.$FEEDBACK_LINK_INCORRECT,
				onClick: () => this.selectIssue(copy.$FEEDBACK_LINK_INCORRECT)
			},
			{
				copy: copy.$FEEDBACK_COULD_NOT_FIND_SHOWTIME,
				onClick: () => this.selectIssue(copy.$FEEDBACK_COULD_NOT_FIND_SHOWTIME)
			}
		];

		return (
			<div class={s.content}>
				<DoubleTickets class={s.doubleTickets} />
				<span class={s.title}>{copy.$FEEDBACK_DESCRIBE_BOOKING_ISSUE}</span>
				{buttons.map(btn => (
					<button class={joinClasses(s.longBtn, bookingIssue && bookingIssue !== btn.copy && s.fadeIssue)} onClick={btn.onClick} key={`feedback-issue-btn-${btn.copy}`}>
						{btn.copy}
					</button>
				))}
				<input
					class={joinClasses(s.otherIssue, bookingIssue && bookingIssue !== this.$otherIssue.value && bookingIssue !== copy.$FEEDBACK_OTHER && s.fadeIssue)}
					type="text"
					ref={($ref) => this.$otherIssue = $ref}
					onInput={(e) => this.selectIssue(e.target.value)}
					onFocus={() => this.selectIssue(this.$otherIssue.value)}
					placeholder={copy.$FEEDBACK_OTHER}
				/>
				<button class={joinClasses(s.sendBtn, showSpinner && s.sending)} onClick={() => this.sendFeedback({ bookingIssue })}>
					{copy.$FEEDBACK_SEND}
					<div class={s.spinner} />
					<span class={joinClasses(s.pleaseSelect, nothingSelectedError && s.show)}>{copy.$FEEDBACK_PLEASE_SELECT}</span>
				</button>
			</div>
		);
	}

	renderThankYouStep() {
		const { timeToClose } = this.state;
		const copy = this.props?.appData?.copy?.baseCopy || {};
		let closeWarning = timeToClose === 1 && copy.$FEEDBACK_CLOSE_TIMER_SINGULAR ? copy.$FEEDBACK_CLOSE_TIMER_SINGULAR : copy.$FEEDBACK_CLOSE_TIMER;
		closeWarning = closeWarning?.replace(/<\d>/g, timeToClose);
		const title = this.apiError ? copy.$FEEDBACK_ERROR : copy.$FEEDBACK_THANK_YOU;

		return (
			<div class={s.content}>
				<Check class={s.checkMark} />
				<span class={s.title}>{title}</span>
				{closeWarning && timeToClose > 0 && <span class={s.closeWarning}>{closeWarning}</span>}
			</div>
		);
	}

	render(props, state) {
		const { step } = state;
		const show = props?.data?.show;

		return (
			<div class={joinClasses(s.container, show && s.show)}>
				<div class={s.bg} onClick={this.closeModal} />
				<div class={s.modal}>
					<button class={s.closeBtn} onClick={this.closeModal}>
						<Close />
					</button>
					{step === this.INITIAL_STEP && this.renderInitialStep()}
					{step === this.BOOKED_TICKETS_STEP && this.renderTicketQuantityStep(step)}
					{step === this.WILL_BOOK_STEP && this.renderTicketQuantityStep(step)}
					{step === this.BOOKING_ISSUE_STEP && this.renderBookingIssueStep()}
					{step === this.THANK_YOU_STEP && this.renderThankYouStep()}
				</div>
			</div>
		);
	}
}