import Store from 'store/store';
import { fire } from 'store/bolt/trackingV2';

Store.on('SHOW_USER_FEEDBACK_MODAL', (bool) => {
	Store.get().userFeedbackModal.set({ show: bool });
});

Store.on('USER_FEEDBACK_MODAL_DISMISSED', () => fire('userFeedbackModalDismissed'));
Store.on('USER_FEEDBACK_MODAL_ISSUE_SUBMITTED', () => fire('userFeedbackModalIssueSubmitted'));
Store.on('USER_FEEDBACK_MODAL_TICKET_FEEDBACK_SUBMITTED', () => fire('userFeedbackModalTicketFeedbackSubmitted'));